<template>
	<div class="shopcont">
		<header>
			<div class="header1">
				<div><img src="../../images/16.png" alt=""></div>
				<div>
					<div>乔梦鑫</div>
					<div>公告:</div>
				</div>
			</div>
			<div class="header2">
				<div>减</div>
				<div>满30减5，满60减8(APP专享)</div>
				<div>2个活动></div>
			</div>
		</header>
		<div class="sp">
			<div>商品</div>
			<div>评价</div>
		</div>
		<div class="content">
			<div class="zuo">
				<div>热销榜</div>
				<div>热销榜</div>
				<div>热销榜</div>
				<div>热销榜</div>
				<div>热销榜</div>
				<div>热销榜</div>
			</div>
			<div class="you">
				<div class="you_div">
					<div><img src="../../images/16.png" alt=""></div>
					<div>
						<div>我问问</div>
						<div>发111</div>
						<div>140评价 月售404份</div>
						<div>￥20</div>
					</div>
					<div>-0</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import axios from 'axios'
	export default {
		name: "Shopcontent"
	}
	mounted:{
	    axios.get("")
	}

</script>

<style scoped lang="scss">
	* {
		margin: 0;
		padding: 0;
		font-size: 0.4rem;
	}

	header {
		width: 100%;
		height: 3.5rem;
		background: #9C7FA6;
		.header1 {
			display: flex;
			padding-top: 0.3rem;
			div:nth-of-type(1) {
				margin-left: 0.5rem;
			}
			div:nth-of-type(2) {
				margin-left: 0.5rem;
				div{
					margin-top: 0.3rem;
					color: white;
				}
			}
		}
		.header2{
			display: flex;
			margin-top: 0.2rem;
			margin-left: 0.5rem;
			color: white;
			div{
				font-size: 0.42rem;
			}
			div:nth-of-type(1){
				width: 0.45rem;
				height: 0.53rem;
				background: red;
				margin-right: 0.44rem;
				line-height: 0.52rem;
			}
			div:nth-of-type(3){
				margin-left: 1.3rem;
			}
		}
	}
	.sp{
		width: 100%;
		height: 1.3rem;
		display: flex;
		justify-content: space-around;
		align-items: center;
		border-bottom: 1px solid #E9E3EB;
		div{
			font-size: 0.5rem;
		}
	}
	.content{
		display: flex;
		.zuo{
			width: 20%;
			border: 1px solid red;
			box-sizing: border-box;
			div{
				height: 1rem;
				text-align: center;
				line-height: 1rem;
				border: 1px solid black;

			}
		}
		.you{
			width: 80%;
			border: 1px solid red;
			box-sizing: border-box;
			.you_div{
				height: 3rem;
				display: flex;
				align-items: center;
				margin-top: 0.2rem;
				padding-left: 0.3rem;
				padding-bottom: 0.2rem;
				border-bottom: 1px solid black;
				>div:nth-of-type(1)>img{
					width: 2.7rem;
					height: 2.7rem;
				}
				>div:nth-of-type(2){
					div{
						margin-top: 0.1rem;
						margin-left: 0.3rem;
					}
					div:nth-of-type(1){
						font-weight: bold;
					}
					div:nth-of-type(2){
						font-size: 0.3rem;
						color: gainsboro;
					}
					div:nth-of-type(4){
						color: red;
					}
				}
				>div:nth-of-type(3){
					margin-left: 0.8rem;
					margin-bottom: 1.7rem;
				}
			}

		}
	}
</style>


